* {
    box-sizing: border-box;
  }
  
  body {
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
      "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
      sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background: #4764ff;
    transition: 0.4s ease-in-out;
  }
  
  html,
  body,
  #root {
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow: hidden;
  }
  
  .scrollArea {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    overflow: auto;
  }
  
  .container {
    margin: 0 auto;
    width: 100%;
    max-width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  header {
    position: absolute;
    z-index: 99;
    width: 100%;
    padding: 25px;
    .header-inner {
      display: flex;
      align-items: center;
      justify-content: space-between;
      .logo {
        font-weight: 900;
        letter-spacing: 2px;
        color: #fff;
      }
      nav {
        ul {
          margin: 0;
          padding: 0;
          display: flex;
          li {
            list-style: none;
            margin: 0 1vw;
            cursor: pointer;
            &.btn {
              a {
                color: #0f37ee;
                font-weight: 600;
                background: #fff;
                padding: 7px 30px;
                border-radius: 50px;
              }
            }
            a {
              text-transform: capitalize;
              text-decoration: none;
              color: #fff;
            }
          }
        }
      }
    }
  }
  
  .title {
    font-size: 10rem;
    z-index: -9999;
    text-align: center;
    width: 100%;
    margin: 0;
    color: #fff;
    span {
      display: block;
    }
    @media (max-width: 1440px) {
      font-size: 6rem;
    }
  }
  
  .loading {
    position: absolute;
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    background: #171717;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
  }
  
  .loading-bar-container {
    width: 100px;
    height: 32px;
    background: #272727;
  }
  
  .loading-bar {
    height: 32px;
    background: #f15946;
  }

  ::-webkit-scrollbar {
    width: 5px;
  }

  ::-webkit-scrollbar-track {
    background: transparent;
  }

  ::-webkit-scrollbar-thumb {
    background: rgba($color: #ffffff, $alpha: 0.5);
    z-index: 100;
    border-radius: 50px;
  }
  
  ::-webkit-scrollbar-thumb:hover {
    background: white;
  }

  canvas {
    z-index: 1000;
  }
